












import Vue from 'vue'

export default Vue.extend({
  props: {
    message: {
      type: String,
      default: 'Are you sure you want to perform this action?'
    },
    action: {
      type: String,
      default: 'Perform Action'
    },
    dismiss: {
      type: String,
      default: 'Cancel'
    }
  },
  methods: {
    emitAction(): void {
      this.$emit('action')
    },
    emitDismiss(): void {
      this.$emit('dismiss')
    }
  }
})
