



















import Vue from 'vue'
import { ITask } from '@/interfaces/ITask'
import { GET_TASK, DELETE_TASK, UPDATE_TASK } from '@/store/actionTypes'
import TopBar from '@/components/TopBar.vue'
import TaskForm from '@/components/TaskForm.vue'
import Dialog from '@/components/Dialog.vue'

export default Vue.extend({
  components: {
    TopBar,
    TaskForm,
    Dialog
  },
  data() {
    return {
      showAlert: false,
      task: {} as ITask
    }
  },
  computed: {
    taskId(): string {
      return this.$route.params.id
    },
    routeTask() {
      const id = this.$route.params.id
      return this.$store.getters[GET_TASK](id)
    },
    allowSave(): boolean {
      return this.task.title ? true : false
    }
  },
  methods: {
    handleUpdate(task: ITask): void {
      this.task = task
    },
    handleCancel(): void {
      this.$router.go(-1)
    },
    handleSave(): void {
      if (this.allowSave) {
        this.$store.dispatch(UPDATE_TASK, this.task)
        this.$router.go(-1)
      }
    },
    displayAlert(): void {
      this.showAlert = true
    },
    dismissAlert(): void {
      this.showAlert = false
    },
    async handleDelete() {
      const status = await this.$store.dispatch(DELETE_TASK, this.taskId)
      if (status.message === 'success') {
        this.dismissAlert()
        this.$router.go(-2)
      }
    }
  }
})
